import axiosInstance from "@/api/axios/axiosInstance";
import { toast } from "sonner";

// Types
export interface ContactMessage {
  id: string;
  contactId: string;
  content: string;
  sender: 'user' | 'contact';
  timestamp: string;
  status: 'sending' | 'sent' | 'delivered' | 'failed';
  messageId?: string;
  twilioSid?: string;
  phoneNumber: string;
  fromPhoneNumber?: string;
  metadata?: Record<string, any>;
}

export interface SendMessageRequest {
  toPhoneNumber: string;
  message: string;
  fromPhoneNumber?: string;
  contactId?: string;
}

export interface UserPhoneNumber {
  id: string;
  number: string;
  provider: string;
  type: string;
  status: string;
  created_at: string;
}

export interface SendMessageResponse {
  success: boolean;
  messageId?: string;
  twilioSid?: string;
  status?: string;
  error?: string;
}

export interface MessageHistoryResponse {
  success: boolean;
  messages: ContactMessage[];
  totalCount: number;
  page: number;
  totalPages: number;
  hasNewMessages?: boolean;
}

/**
 * Send SMS message to a contact
 */
export async function sendSMSToContact(data: SendMessageRequest): Promise<SendMessageResponse> {
  try {
    console.log('üì± Sending SMS to contact:', data);

    // Use the correct contact messaging API endpoint
    const response = await axiosInstance.post(`/api/contact-messages/contacts/${data.contactId}/messages`, {
      content: data.message,
      fromPhoneNumber: data.fromPhoneNumber
    });

    if (response.data.success) {
      toast.success('Message sent successfully');
      return {
        success: true,
        messageId: response.data.data?.messageId,
        twilioSid: response.data.data?.twilioSid,
        status: response.data.data?.status
      };
    } else {
      throw new Error(response.data.message || 'Failed to send message');
    }
  } catch (error: any) {
    console.error('Error sending SMS:', error);
    const message = error.response?.data?.message || error.message || 'Failed to send message';
    toast.error(message);
    
    return {
      success: false,
      error: message
    };
  }
}

/**
 * Get message history for a contact
 */
export async function getContactMessages(
  contactId: string, 
  page: number = 1, 
  limit: number = 50
): Promise<MessageHistoryResponse> {
  try {
    const response = await axiosInstance.get(`/api/contact-messages/contacts/${contactId}/messages`, {
      params: { page, limit }
    });

    return {
      success: true,
      messages: response.data.messages || [],
      totalCount: response.data.totalCount || 0,
      page: response.data.page || 1,
      totalPages: response.data.totalPages || 1
    };
  } catch (error: any) {
    console.error('Error fetching contact messages:', error);
    const message = error.response?.data?.message || 'Failed to fetch messages';
    toast.error(message);
    
    return {
      success: false,
      messages: [],
      totalCount: 0,
      page: 1,
      totalPages: 1
    };
  }
}

/**
 * Check for new incoming messages only (efficient polling)
 */
export async function checkNewIncomingMessages(
  contactId: string,
  lastMessageId?: string
): Promise<MessageHistoryResponse> {
  try {
    console.log(`üîç Checking new incoming messages for contact ${contactId}, after message ${lastMessageId}`);

    const params: any = {};
    if (lastMessageId) {
      params.lastMessageId = lastMessageId;
    }

    const response = await axiosInstance.get(`/api/contact-messages/contacts/${contactId}/new-messages`, {
      params
    });

    return {
      success: true,
      messages: response.data.data || [],
      hasNewMessages: response.data.hasNewMessages || false,
      totalCount: response.data.data?.length || 0,
      page: 1,
      totalPages: 1
    };
  } catch (error: any) {
    console.error('Error checking new incoming messages:', error);
    // Don't show toast for polling errors to avoid spam

    return {
      success: false,
      messages: [],
      hasNewMessages: false,
      totalCount: 0,
      page: 1,
      totalPages: 1
    };
  }
}

/**
 * Save message to database
 */
export async function saveMessage(messageData: Partial<ContactMessage>): Promise<ContactMessage | null> {
  try {
    const response = await axiosInstance.post('/api/contact-messages/messages', messageData);
    
    if (response.data.success) {
      return response.data.message;
    } else {
      throw new Error(response.data.message || 'Failed to save message');
    }
  } catch (error: any) {
    console.error('Error saving message:', error);
    const message = error.response?.data?.message || 'Failed to save message';
    toast.error(message);
    return null;
  }
}

/**
 * Update message status
 */
export async function updateMessageStatus(
  messageId: string, 
  status: ContactMessage['status']
): Promise<boolean> {
  try {
    const response = await axiosInstance.patch(`/api/contact-messages/messages/${messageId}`, {
      status
    });

    return response.data.success;
  } catch (error: any) {
    console.error('Error updating message status:', error);
    return false;
  }
}

/**
 * Get all conversations (contacts with recent messages)
 */
export async function getConversations(page: number = 1, limit: number = 20) {
  try {
    const response = await axiosInstance.get('/api/contact-messages/conversations', {
      params: { page, limit }
    });

    return {
      success: true,
      conversations: response.data.conversations || [],
      totalCount: response.data.totalCount || 0,
      page: response.data.page || 1,
      totalPages: response.data.totalPages || 1
    };
  } catch (error: any) {
    console.error('Error fetching conversations:', error);
    const message = error.response?.data?.message || 'Failed to fetch conversations';
    toast.error(message);
    
    return {
      success: false,
      conversations: [],
      totalCount: 0,
      page: 1,
      totalPages: 1
    };
  }
}

/**
 * Test Twilio connection
 */
export async function testTwilioConnection() {
  try {
    const response = await axiosInstance.get('/api/text-webhook/test-connection');
    
    if (response.data.success) {
      toast.success('Twilio connection successful');
      return {
        success: true,
        accountName: response.data.data?.accountName,
        accountSid: response.data.data?.accountSid
      };
    } else {
      throw new Error(response.data.message || 'Connection test failed');
    }
  } catch (error: any) {
    console.error('Error testing Twilio connection:', error);
    const message = error.response?.data?.message || 'Connection test failed';
    toast.error(message);
    
    return {
      success: false,
      error: message
    };
  }
}

/**
 * Format phone number to E.164 format
 */
export function formatPhoneToE164(phone: string): string {
  // Remove all non-digit characters except +
  const cleaned = phone.replace(/[^\d+]/g, '');
  
  // If it starts with +, return as is
  if (cleaned.startsWith('+')) {
    return cleaned;
  }
  
  // If it's 10 digits, assume US number
  if (cleaned.length === 10) {
    return `+1${cleaned}`;
  }
  
  // If it's 11 digits and starts with 1, assume US number
  if (cleaned.length === 11 && cleaned.startsWith('1')) {
    return `+${cleaned}`;
  }
  
  // Otherwise, add + if not present
  return cleaned.startsWith('+') ? cleaned : `+${cleaned}`;
}

/**
 * Validate phone number format
 */
export function isValidPhoneNumber(phone: string): boolean {
  const e164Pattern = /^\+[1-9]\d{1,14}$/;
  const formatted = formatPhoneToE164(phone);
  return e164Pattern.test(formatted);
}

/**
 * Format phone number for display
 */
export function formatPhoneForDisplay(phone: string): string {
  const cleaned = phone.replace(/[^\d]/g, '');
  
  if (cleaned.length === 10) {
    return `(${cleaned.slice(0, 3)}) ${cleaned.slice(3, 6)}-${cleaned.slice(6)}`;
  }
  
  if (cleaned.length === 11 && cleaned.startsWith('1')) {
    const withoutCountryCode = cleaned.slice(1);
    return `+1 (${withoutCountryCode.slice(0, 3)}) ${withoutCountryCode.slice(3, 6)}-${withoutCountryCode.slice(6)}`;
  }
  
  return phone;
}

/**
 * Get user's phone numbers for messaging
 */
export async function getUserPhoneNumbers(): Promise<UserPhoneNumber[]> {
  try {
    const response = await axiosInstance.get('/api/contact-messages/user/phone-numbers');

    if (response.data.success) {
      return response.data.data;
    } else {
      throw new Error(response.data.message || 'Failed to get phone numbers');
    }
  } catch (error: any) {
    console.error('Error getting user phone numbers:', error);
    toast.error(error.response?.data?.message || 'Failed to get phone numbers');
    throw error;
  }
}

export default {
  sendSMSToContact,
  getContactMessages,
  saveMessage,
  updateMessageStatus,
  getConversations,
  testTwilioConnection,
  formatPhoneToE164,
  isValidPhoneNumber,
  formatPhoneForDisplay,
  getUserPhoneNumbers
};
